'use strict';

// MODULES //

import FLOAT16_SIGNIFICAND_MASK from '@stdlib/constants-float16-significand-mask';
import getWord from '@stdlib/number-float16-base-to-word';


// MAIN //

/**
* Returns an integer corresponding to the significand of a half-precision floating-point number.
*
* @param {number} x - half-precision floating-point number
* @returns {uinteger16} significand
*
* @example
* import toFloat16 from '@stdlib/number-float64-base-to-float16';
*
* var s = significand( toFloat16( 3.14 ) ); // => 1001001000
* // returns 584
*
* @example
* import toFloat16 from '@stdlib/number-float64-base-to-float16';
*
* var s = significand( toFloat16( 3.14e-6 ) ); // => 0000110101
* // returns 53
*
* @example
* import toFloat16 from '@stdlib/number-float64-base-to-float16';
*
* var s = significand( toFloat16( -3.14 ) ); // => 1001001000
* // returns 584
*
* @example
* var s = significand( 0.0 ); // => 0000000000
* // returns 0
*
* @example
* var s = significand( NaN ); // => 1000000000
* // returns 512
*/
function significand( x ) {
	// Convert `x` to an unsigned 16-bit integer corresponding to the IEEE 754 binary representation:
	var w = getWord( x );

	// Apply a mask to isolate only the significand bits:
	return w & FLOAT16_SIGNIFICAND_MASK;
}


// EXPORTS //

export default significand;
